<script>
import { mapState, mapGetters } from 'vuex'
import { Field, Button } from 'mint-ui'
import { thirdPartyLoginRequest,
         getMobilePhoneCodeRequest,
         mobileLoginRequest,
         wechatLoginRequest,
         alipayLoginRequest
} from '../../service/method.js'
import { thirdPartyLoginResponse,
         mobileLoginResponse,
         setSystemLanguageResponse,
         getMobilePhoneCodeResponse,
         wechatLoginResponse,
         alipayLoginResponse
} from '../../service/getData.js'
import toast from '../../components/common/toast'

export default {
  data () {
    return {
      phone: '', // 手机号码
      code: '', // 手机验证码
      codeText: '验证',
      loginWay: [
        {
          software: 'Facebook',
          icon: require('../../image/login/facebook.png')
        },
        {
          software: 'Twitter',
          icon: require('../../image/login/twitter.png')
        },
        {
          software: 'Google',
          icon: require('../../image/login/google.png')
        }
      ], // 第三方登录方式
      timer: null, // 定时器
      fullHeight: null, // 屏幕高度
      keyboard: false, // 软键盘是否弹出
      offsetTop: 0, // 偏移量（当软键盘遮住输入框偏移）
      logining: false, // 登录中
      loginAlert: false
    }
  },

  mounted () {
    this.onResize()
    // // 查询系统语言返回
    // systemLanguageResponse(this.NAVIGATOR, this)

    // 设置系统语言返回
    setSystemLanguageResponse(this.NAVIGATOR, this)

    // 第三方登录返回
    thirdPartyLoginResponse(this.NAVIGATOR, this)

    // 获取手机验证码返回
    getMobilePhoneCodeResponse(this.NAVIGATOR, this)

    // 手机登录返回
    mobileLoginResponse(this.NAVIGATOR, this)

    // 微信登录返回
    wechatLoginResponse(this.NAVIGATOR, this)

    // 支付宝登录返回
    alipayLoginResponse(this.NAVIGATOR, this)

    // // 查询系统语言
    // systemLanguageRequest(this.NAVIGATOR, this)

    if (this.$root.ISDEBUG) {
      //   window.thirdPartyLoginResponse(true, '{"linkURL":"","middleName":"","lastName":"","userID":"156821558496182","firstName":"","refreshDate":"2018-06-04 16:09:21","name":"WEB_VIEW"}')
      setTimeout(() => {
        // window.getMobilePhoneCodeResponse(false)
        // window.mobileLoginResponse(false, '0x80180')
      }, 5000)
      //   window.systemLanguageResponse('en')

      //   window.setSystemLanguageResponse(true, 'zh')
    }
  },

  components: {
    Field,
    Button,
    toast
  },

  computed: {
    ...mapState([
      'userInfo', 'ToastType', 'currentRouter'
    ]),

    ...mapGetters(['errorCode']),

    // 验证状态
    validate () {
      let vPhone = /^\d{11}$/g.test(this.phone)
      let vText = this.codeText === '验证'
      let vCode = /^.{1,}$/g.test(this.code)
      return {
        codeActive: vPhone && vText,
        loginActive: vPhone && vCode
      }
    }
  },

  methods: {
    navJump () {
      this.$router.go(-1)
    },

    onResize () {
      // 初始化延时，否则fullHeight值为0
      setTimeout(() => {
        let h = 0
        this.fullHeight = document.documentElement.clientHeight
        window.onresize = () => {
          h = document.documentElement.clientHeight
          // 软键盘是否弹起
          if (h < this.fullHeight) {
            this.keyboard = true
          } else {
            this.keyboard = false
          }
          // 解决小屏幕遮挡输入框
          if (h < 340) {
            this.offsetTop = 340 - h
          } else {
            this.offsetTop = 0
          }
        }
      }, 100)
    },

    selectItem (num) {
      switch (num) {
        case 1:
          // 微信登录
          wechatLoginRequest(this.NAVIGATOR, this)
          break
        case 2:
          // 支付宝登录
          alipayLoginRequest(this.NAVIGATOR, this)
          break
        case 3:
          this.loginAlert = true
          break
      }
    },

    closeHandle () {
      this.loginAlert = false
    },

    getCode () {
      if (this.codeText === '验证') {
        // 获取验证码
        getMobilePhoneCodeRequest(this.NAVIGATOR, this, this.phone)
        // this.codeText = '获取中'
        this.countdown()
        if (this.$root.ISDEBUG) {
          setTimeout(() => {
            window.getMobilePhoneCodeResponse(true)
          }, 2000)
        }
      }
    },

    // 获取验证码成功后的回调
    countdown () {
      let num = 60
      this.codeText = `${num}s`
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.codeText = `${--num}s`
        if (num === 0) {
          this.codeText = '验证'
          clearInterval(this.timer)
        }
      }, 1000)
    },

    login () {
      mobileLoginRequest(this.NAVIGATOR, this, this.phone, this.code)
      this.logining = true
      if (this.$root.ISDEBUG) {
        setTimeout(() => {
          window.mobileLoginResponse(true)
        }, 2000)
      }
    },

    thirdPartyLogin (item) {
      thirdPartyLoginRequest(this.NAVIGATOR, this, item.software)
    },

    setLanguage () {
      this.$router.push({path: '/login/language'})
    },

    emailLogin () {
      this.$router.push({path: '/login/emailLogin'})
    }
  },
  watch: {
    errorCode (newValue, oldValue) {
      if (!newValue) {
        clearInterval(this.timer)
        this.codeText = '验证'
        this.logining = false
      }
    },

    currentRouter: function (newValue) {
      if (newValue === '/login') {
        this.onResize()
      }
    }
  }
}
</script>

