<template>
  <div class="state">
    <div class="state-container" @click="clickHandle">
      <div class="table">
        <div class="conent">
          <div class="view1" v-show="viewSwitch===1">
            <div id="myChart"></div>
            <div class="nav">
              <div class="list">
                <li @click="navHandle(1)">{{$t('addPage.hour')}}<b v-show="nav===1"></b></li>
                <li @click="navHandle(2)">{{$t('addPage.day')}}<b v-show="nav===2"></b></li>
                <li @click="navHandle(3)">{{$t('addPage.month')}}<b v-show="nav===3"></b></li>
              </div>
              <div class="time">
                <span v-show="nav===1">{{$t('addPage.last')}}<b>24</b>{{$t('addPage.hour')}}</span>
                <span v-show="nav===2">{{$t('addPage.last')}}<b>7</b>{{$t('addPage.day')}}</span>
                <span v-show="nav===3">{{$t('addPage.last')}}<b>7</b>{{$t('addPage.month')}}</span>
              </div>
            </div>
            <div class="realTime">
              <ul>
                <li>
                  <span>{{realTime.voltage}}</span>
                  <strong>V</strong>
                </li>
                <li>
                  <span>{{realTime.electricity}}</span>
                  <strong>A</strong>
                </li>
                <li>
                  <span>{{realTime.powerValue}}</span>
                  <strong>W</strong>
                </li>
                <li>
                  <span>{{realTime.frequency}}</span>
                  <strong>Hz</strong>
                </li>
                <li>
                  <span>{{realTime.powerFactor}}</span>
                  <strong>Pf</strong>
                </li>
              </ul>
            </div>
          </div>
          <div class="view2" v-show="viewSwitch===2">
            <div class="container">
              <ul class="left">
                <li>
                  Peak Volt
                  <span>{{maxVoltage.toFixed(3)}}</span>
                </li>
                <li>
                  Peak Watts
                  <span>{{MaximumPower.toFixed(3)}}</span>
                </li>
                <li>
                  <img src="../../image/state/wifi/timing@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: countdown.percentage + '%'}"></div>
                  </div>
                  <span>{{countdown.hour}}<b>h</b>{{countdown.minute}}<b>m</b>{{countdown.second}}<b>s</b></span>
                </li>
                <li>
                  <img src="../../image/state/wifi/fixedtile@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: progressValue.powerConsumption}"></div>
                  </div>
                  <span>{{eAlarmValue - electricityValue}}<b>W</b></span>
                </li>
                <li>
                  <img src="../../image/state/wifi/fixedfee@3x.png" alt="">
                  <div class="progress">
                    <div class="per" :style="{width: progressValue.spend}"></div>
                  </div>
                  <span>{{sAlarmValue - spendValue}}<b>$</b></span>
                </li>
                <li>
                  <div class="load">OVERLOAD</div>
                  <span>{{overload}}<b></b></span>
                </li>
              </ul>
              <ul class="right">
                <li>Running time</li>
                <li>
                  {{running.hour}}<b>h</b>
                  {{running.minute}}<b>m</b>
                  {{running.second}}<b>s</b>
                </li>
                <li>
                  <div class="box">
                    <span>{{rate}}</span>
                    <strong>RATE</strong>
                  </div>
                  <div class="num">
                    {{ratePrice}}<span>$</span>
                  </div>
                </li>
                <li>
                  <div class="box">GHG</div>
                  <div class="num">
                    {{GHG}}<span>kg</span>
                  </div>
                </li>
                <li>
                  <div class="box">
                    <div class="top">
                      <span>{{nowTime.date}}</span>
                      <strong>{{nowTime.week}}</strong>
                    </div>
                    <div class="bottom">
                      <span>
                        <em>AM</em>
                        <b>PM</b>
                      </span>
                      <strong>
                        <em>{{nowTime.time}}</em>
                      </strong>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="view3" v-show="viewSwitch===3">
            <div id="Pillar"></div>
            <div class="nav">
              <ul>
                <li @click="navHandle(4)" :class="{activeColor: pillarNav===1}">
                  Day
                  <b></b>
                </li>
                <li @click="navHandle(5)" :class="{activeColor: pillarNav===2}">
                  Week
                  <b></b>
                </li>
                <li @click="navHandle(6)" :class="{activeColor: pillarNav===3}">
                  Month
                  <b></b>
                </li>
                <li @click="navHandle(7)" :class="{activeColor: pillarNav===4}">
                  Year
                  <b></b>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="button">
        <ul>
          <li @click="seleteItem(1)">
            <img src="../../image/state/wifi/realtime@3x.png" alt="">
          </li>
          <li @click="seleteItem(2)">
            <img src="../../image/state/wifi/data@3x.png" alt="">
          </li>
          <li @click="seleteItem(3)">
            <img src="../../image/state/wifi/history@3x.png" alt="">
          </li>
          <li @click="seleteItem(4)">
            <img src="../../image/state/wifi/onoff@3x.png" alt="">
            <span>
              <img src="../../image/state/wifi/off@3x.png" v-show="!socketSwitch" alt="">
              <img src="../../image/state/wifi/on@3x.png" v-show="socketSwitch" alt="">
            </span>
          </li>
          <li @click="seleteItem(5)">
            <img src="../../image/state/wifi/config@3x.png" alt="">
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
import { mapState } from 'vuex'
import { numToTimeFormat, digitFormat, weekEnglish } from '../../config/utils.js'
import {
  deviceHistoryDataRequest,
  setStatusBarRequest,
  socketStatusRequest,
  countdownDataRequest,
  spendingCountdownDataRequest,
  queryAlarmPowerRequest,
  queryAlarmCurrentRequest,
  deviceAccumulationParameterRequest,
  deviceRateRequest,
  powerSwitchRequest
} from '../../service/method.js'
import {
  deviceHistoryDataResponse,
  socketStatusResponse,
  countdownDataResponse,
  spendingCountdownDataResponse,
  queryAlarmPowerResponse,
  queryAlarmCurrentResponse,
  deviceAccumulationParameterResponse,
  deviceRateResponse
} from '../../service/getData.js'
if (process.env.NODE_ENV === 'development') {
  var Data = require('./data.js')
  // console.log(Data)
}
export default {
  data () {
    return {
      nav: 1, // 1 时、2 日、3 月
      pillarNav: 1, // 1 日、2 周、3 月、4 年
      viewSwitch: 1, // 1 曲线图、2 表、3 柱状图
      xData: {
        month: [],
        day: [],
        hour: [],
        pillarDay: [],
        pillarWeek: [],
        pillarMonth: [],
        pillarYear: []
      },
      yData: {
        month: [],
        day: [],
        hour: [],
        pillarDay: [],
        pillarWeek: [],
        pillarMonth: [],
        pillarYear: []
      },
      progress: {
        time: '50%',
        powerConsumption: '60%',
        spend: '20%'
      },
      myChart: '', // echarts图表
      pillarEchart: '', // echarts柱状图
      maxVoltage: 0, // 最大电压
      MaximumPower: 0, // 最大功率
      electricityValue: 0, // 电量值
      eAlarmValue: 0, // 电量告警值
      spendValue: 0, // 花费值
      sAlarmValue: 0, // 花费告警值
      rate: 1, // 费率
      ratePrice: 0, // 价格
      runningTime: 0, // 运行时间
      GHG: 0, // GHG量
      clock: 0,
      realTime: {
        voltage: 0,
        electricity: 0,
        powerValue: 0,
        frequency: 0,
        powerFactor: 0
      }, // 电压 电流 功率 频率 功率因素
      timer: ''
    }
  },
  computed: {
    ...mapState([
      'currentDevice', 'statePage', 'settingData', 'jumpTimer', 'socketSwitch'
    ]),
    countdown () {
      if (this.statePage.countdownText <= 0) {
        this.$store.state.statePage.countdownText = 0
      }
      // console.log(this.statePage.countdownText)
      let Percentage = (this.statePage.countdownText / (this.statePage.totalCountdown * 60)) * 100
      console.log(Percentage)
      let countdown = new Date(0, 0, 0, 0, 0, this.statePage.countdownText)
      let h = countdown.getHours()
      let m = countdown.getMinutes()
      let s = countdown.getSeconds()
      // console.log(h, m, s)
      return {
        hour: digitFormat(h),
        minute: digitFormat(m),
        second: digitFormat(s),
        percentage: Percentage
      }
    },
    progressValue () {
      return {
        powerConsumption: parseInt((this.eAlarmValue - this.electricityValue) / this.eAlarmValue * 100) + '%',
        spend: parseInt((this.sAlarmValue - this.spendValue) / this.sAlarmValue * 100) + '%'
      }
    },
    overload () {
      return this.settingData.voltage + 'W' || this.settingData.electricity + 'A'
    },
    nowTime () {
      let date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth()
      let day = date.getDate()
      let hour = date.getHours()
      let minute = date.getMinutes()
      let second = date.getSeconds()
      clearTimeout(this.timer)
      this.clock = new Date(year, month, day, hour, minute, second)
      this.timer = setTimeout(() => {
        this.clock = new Date(year, month, day, hour, minute, ++second)
        // console.log(this.clock)
      }, 1000)
      return {
        date: `${digitFormat(this.clock.getFullYear())}
              /${digitFormat(this.clock.getMonth() + 1)}
              /${digitFormat(this.clock.getDate())}`,
        time: `${digitFormat(this.clock.getHours())}
              :${digitFormat(this.clock.getMinutes())}
              :${digitFormat(this.clock.getSeconds())}`,
        week: weekEnglish(this.clock.getDay())
      }
    },
    running () {
      let hour = parseInt(this.runningTime / 1000 / 60 / 60)
      let minute = parseInt((this.runningTime - hour * 60 * 60 * 1000) / 1000 / 60)
      let second = parseInt((this.runningTime - hour * 60 * 60 * 1000 - minute * 60 * 1000) / 1000)
      return {
        hour: digitFormat(hour),
        minute: digitFormat(minute),
        second: digitFormat(second)
      }
    }
  },
  mounted () {
    // 查询设备历史数据返回
    deviceHistoryDataResponse(this.NAVIGATOR, this)

    // 插座状态数据返回
    socketStatusResponse(this.NAVIGATOR, this)

    // 插座状态数据
    socketStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac, true)

    // 倒计时数据返回
    countdownDataResponse(this.NAVIGATOR, this)

    let timer1 = setTimeout(() => {
      // 请求倒计时数据
      countdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer1)
    }, 300)

    // 定量定费数据返回
    spendingCountdownDataResponse(this.NAVIGATOR, this)

    let timer2 = setTimeout(() => {
      // 定量定费数据请求
      spendingCountdownDataRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer2)
    }, 600)

    // 查询功率警告值返回
    queryAlarmPowerResponse(this.NAVIGATOR, this)

    let timer3 = setTimeout(() => {
      // 查询功率警告值请求
      queryAlarmPowerRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer3)
    }, 900)

    // 查询电流警告值返回
    queryAlarmCurrentResponse(this.NAVIGATOR, this)

    let timer4 = setTimeout(() => {
      // 查询电流警告值请求
      queryAlarmCurrentRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer4)
    }, 1200)

    // 查询设备累积参数返回
    deviceAccumulationParameterResponse(this.NAVIGATOR, this)

    let timer5 = setTimeout(() => {
      // 查询设备累积参数
      deviceAccumulationParameterRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer5)
    }, 1500)

    // 查询设备费率返回
    deviceRateResponse(this.NAVIGATOR, this)

    let timer6 = setTimeout(() => {
      // 查询设备费率请求
      deviceRateRequest(this.NAVIGATOR, this, this.currentDevice.mac)
      clearTimeout(timer6)
    }, 1800)

    let timer9 = setTimeout(() => {
      this.echartsInit('hour')
      clearTimeout(timer9)
    }, 350)

    // 设置手机状态栏显示
    setStatusBarRequest(this.NAVIGATOR, this, false, '')

    if (this.$root.ISDEBUG) {
      // this.makePillarEchart()
      setTimeout(() => {
        // window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":10,"allTime":10,"Switchgear":true,"countdownSwitch":true}')
        window.countdownDataResponse(this.currentDevice.mac, '{"hour":0,"minute":1,"allTime":2,"Switchgear":true,"countdownSwitch":false}')
        // setTimeout(() => {
        //   console.log(123156)
        //   window.countdownDataResponse(this.currentDevice.mac,'{"hour":0,"minute":0,"allTime":0,"Switchgear":false,"countdownSwitch":false}')
        // }, 5000)

        window.spendingCountdownDataResponse(this.currentDevice.mac, true, '{"power":{"currentValue":20,"alarmValue":50,"alarmSwitch":true,"year":2018,"month":6,"day":6},"cost":{"currentValue":30,"alarmValue":40,"alarmSwitch":false,"year":2018,"month":1,"day":16}}')

        window.deviceAccumulationParameterResponse(this.currentDevice.mac, 1026645, 180, 120)

        window.deviceRateResponse(this.currentDevice.mac, 1, 0, 13.8, 0, 0, 0)
      }, 3000)
    }
  },
  methods: {
    // 点击后清除跳转计时器
    clickHandle () {
      clearTimeout(this.jumpTimer)
    },

    seleteItem (num) {
      switch (num) {
        case 1:
          this.viewSwitch = num
          let timer1 = setTimeout(() => {
            if (this.yData.hour.length === 0) {
              this.echartsInit('hour')
            } else {
              this.makeEchart(this.xData.hour, this.yData.hour)
            }
            clearTimeout(timer1)
          }, 10)
          break
        case 2:
          this.viewSwitch = num
          break
        case 3:
          this.viewSwitch = num
          let timer2 = setTimeout(() => {
            if (this.yData.pillarDay.length === 0) {
              this.echartsInit('pillarDay')
            } else {
              this.makePillarEchart(this.xData.pillarDay, this.yData.pillarDay)
            }
            clearTimeout(timer2)
          }, 10)
          break
        case 4:
          // 开启关闭电源
          powerSwitchRequest(this.NAVIGATOR, this, this.$store.state.currentDevice.mac, !this.socketSwitch)
          if (this.$root.ISDEBUG) {
            window.powerSwitchResponse(this.currentDevice.mac, !this.socketSwitch)
          }
          break
        case 5:
          this.$router.go(-1)
          // 插座状态数据
          socketStatusRequest(this.NAVIGATOR, this, this.currentDevice.mac, false)
          // 设置手机状态栏显示
          setStatusBarRequest(this.NAVIGATOR, this, true, '#4EA3FE')
          break
      }
    },

    // echarts表导航按钮
    navHandle (num) {
      this.myChart.clear()
      switch (num) {
        case 1:
          this.nav = 1
          if (this.yData.hour.length === 0) {
            this.echartsInit('hour')
          } else {
            this.makeEchart(this.xData.hour, this.yData.hour)
          }
          break
        case 2:
          this.nav = 2
          if (this.yData.day.length === 0) {
            this.echartsInit('day')
          } else {
            this.makeEchart(this.xData.day, this.yData.day)
          }
          break
        case 3:
          this.nav = 3
          if (this.yData.month.length === 0) {
            this.echartsInit('month')
          } else {
            this.makeEchart(this.xData.month, this.yData.month)
          }
          break
        case 4:
          this.pillarNav = 1
          this.makePillarEchart(this.xData.pillarDay, this.yData.pillarDay)
          break
        case 5:
          this.pillarNav = 2
          if (this.yData.pillarWeek.length === 0) {
            this.echartsInit('pillarWeek')
          } else {
            this.makePillarEchart(this.xData.pillarWeek, this.yData.pillarWeek)
          }
          break
        case 6:
          this.pillarNav = 3
          if (this.yData.pillarMonth.length === 0) {
            this.echartsInit('pillarMonth')
          } else {
            this.makePillarEchart(this.xData.pillarMonth, this.yData.pillarMonth)
          }
          break
        case 7:
          this.pillarNav = 4
          if (this.yData.pillarYear.length === 0) {
            this.echartsInit('pillarYear')
          } else {
            this.makePillarEchart(this.xData.pillarYear, this.yData.pillarYear)
          }
          break
      }
    },

    // echarts初始化
    echartsInit (type) {
      console.log('echartsInit')
      let customDate
      let customEndDate
      let nowDate
      let startTime
      let endTime
      let current
      let nowYear = new Date().getFullYear()
      let nowMonth = new Date().getMonth() + 1
      let nowWeek = new Date().getDay()
      nowWeek = (nowWeek === 0) ? 7 : nowWeek
      let nowDay = new Date().getDate()
      switch (type) {
        case 'month':
          this.xData.month = []
          this.yData.month = []
          // 设备最近7月
          customDate = new Date(nowYear, nowMonth - 6, 1)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth() + 1}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')

          // 到下个月开始前
          endTime = `${nowYear}/${nowMonth + 1}/1`
          // 时间段含五分钟间隔
          let length = (+new Date(endTime) - +new Date(startTime)) / 1000 / 60 / 60 / 24
          console.log(startTime)
          console.log(endTime)
          // console.log(length)
          for (let i = 0; i < length; i++) {
            customDate = new Date(nowYear, nowMonth - 6, 1 + i)
            current = `${customDate.getMonth() + 1}
                      /${customDate.getDate()}`
            current = current.replace(/\s/g, '')

            this.xData.month.push(current)
            this.yData.month.push(0)
          }
          console.log(this.xData.month)
          this.makeEchart(this.xData.month, this.yData.month)
          // console.log(tomorrow)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 3)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response3')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 210, 3, Data.monthData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'day':
          this.xData.day = []
          this.yData.day = []
          // 设备最近7天
          customDate = new Date(nowYear, nowMonth, nowDay - 6)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowYear}
                    /${nowMonth}
                    /${nowDay}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = 0; i < 168; i++) {
            customDate = new Date(nowYear, nowMonth, nowDay - 6, 0 + i, 0)
            current = `${customDate.getDate()}
                      日${digitFormat(customDate.getHours())}
                      :${digitFormat(customDate.getMinutes())}`
            current = current.replace(/\s/g, '')
            this.xData.day.push(current)
            this.yData.day.push(0)
          }
          this.makeEchart(this.xData.day, this.yData.day)
          // console.log(endTime)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 2)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response2')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 7, 2, Data.dayData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'hour':
          this.xData.hour = []
          this.yData.hour = []
          // 设备24小时历史数据
          customDate = new Date(nowYear, nowMonth, nowDay - 1)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowYear}
                    /${nowMonth}
                    /${nowDay}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = 0; i < 288; i++) {
            current = numToTimeFormat(i * 5)
            this.xData.hour.push(current)
            this.yData.hour.push(0)
          }
          this.makeEchart(this.xData.hour, this.yData.hour)
          // console.log(endTime)
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 1)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response1')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, 1, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'pillarDay':
          this.xData.pillarDay = []
          this.yData.pillarDay = []
          // 天历史数据
          customDate = new Date(nowYear, nowMonth, nowDay - 15)
          nowDate = new Date(nowYear, nowMonth, nowDay - 1)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowDate.getFullYear()}
                      /${nowDate.getMonth()}
                      /${nowDate.getDate()}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = -1; i >= -15; --i) {
            customDate = new Date(nowYear, nowMonth, nowDay + i)
            current = `${customDate.getDate()}日`
            this.xData.pillarDay.unshift(current)
            this.yData.pillarDay.unshift(1)
          }
          this.makePillarEchart(this.xData.pillarDay, this.yData.pillarDay)
          // 查询前十五天数据
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 3)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response4')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, 3, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'pillarWeek':
          this.xData.pillarWeek = []
          this.yData.pillarWeek = []
          // 周历史数据
          customDate = new Date(nowYear, nowMonth, nowDay - nowWeek + (-15 * 7) + 1)
          nowDate = new Date(nowYear, nowMonth, nowDay - nowWeek)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowDate.getFullYear()}
                      /${nowDate.getMonth()}
                      /${nowDate.getDate()}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = -1; i >= -15; --i) {
            customDate = new Date(nowYear, nowMonth, nowDay - nowWeek + (i * 7) + 1)
            customEndDate = new Date(nowYear, nowMonth, nowDay - nowWeek + (i * 7) + 7)
            current = `${customDate.getMonth()}
                      /${customDate.getDate()}
                      -${customEndDate.getMonth()}
                      /${customEndDate.getDate()}`
            current = current.replace(/\s/g, '')
            this.xData.pillarWeek.unshift(current)
            this.yData.pillarWeek.unshift(1)
          }
          this.makePillarEchart(this.xData.pillarWeek, this.yData.pillarWeek)
          // 查询前十五星期数据
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 4)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response5')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, 4, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'pillarMonth':
          this.xData.pillarMonth = []
          this.yData.pillarMonth = []
          // 月历史数据
          customDate = new Date(nowYear, nowMonth - 15, 1)
          nowDate = new Date(nowYear, nowMonth, 1)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowDate.getFullYear()}
                      /${nowDate.getMonth()}
                      /${nowDate.getDate()}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = -1; i >= -15; --i) {
            customDate = new Date(nowYear, nowMonth + i, nowDay)
            current = `${customDate.getFullYear()}/${customDate.getMonth()}`
            this.xData.pillarMonth.unshift(current)
            this.yData.pillarMonth.unshift(1)
          }
          this.makePillarEchart(this.xData.pillarMonth, this.yData.pillarMonth)
          // 查询前十五月数据
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 5)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response6')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, 5, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
        case 'pillarYear':
          this.xData.pillarYear = []
          this.yData.pillarYear = []
          // 月历史数据
          customDate = new Date(nowYear - 15, 1, 1)
          nowDate = new Date(nowYear, 1, 1)
          startTime = `${customDate.getFullYear()}
                      /${customDate.getMonth()}
                      /${customDate.getDate()}`
          startTime = startTime.replace(/\s/g, '')
          endTime = `${nowDate.getFullYear()}
                      /${nowDate.getMonth()}
                      /${nowDate.getDate()}`
          endTime = endTime.replace(/\s/g, '')
          console.log(startTime)
          console.log(endTime)
          for (let i = -1; i >= -15; --i) {
            customDate = new Date(nowYear + i, nowMonth, nowDay)
            current = `${customDate.getFullYear()}年`
            this.xData.pillarYear.unshift(current)
            this.yData.pillarYear.unshift(1)
          }
          this.makePillarEchart(this.xData.pillarYear, this.yData.pillarYear)
          // 查询前十五年数据
          deviceHistoryDataRequest(this.NAVIGATOR, this, this.currentDevice.mac, startTime, endTime, 6)
          if (this.$root.ISDEBUG) {
            let timer = setTimeout(() => {
              console.log('Response7')
              window.deviceHistoryDataResponse(this.currentDevice.mac, startTime, 1, 6, Data.hourData)
              clearTimeout(timer)
            }, 2000)
          }
          break
      }
    },

    // 绘制echarts折线图
    makeEchart (xData, yData) {
      console.log('makeEchart')
      this.myChart = echarts.init(document.getElementById('myChart'))
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          position: function (pos, params, dom, rect, size) {
            // 鼠标在左侧时 tooltip 显示到右侧，鼠标在右侧时 tooltip 显示到左侧。
            var obj = {top: '6%'}
            if (pos[0] < size.viewSize[0] / 2) {
              obj['right'] = '8%'
            } else {
              obj['left'] = '16%'
            }
            return obj
          },
          confine: true, // 是否将 tooltip 框限制在图表的区域内
          textStyle: {
            // fontFamily: ''
          }
        },
        legend: {
          x: '4%',
          top: '5%',
          textStyle: {
            color: 'red'
          },
          'data': ['时', '日', '月']
        },
        grid: {
          // 直角坐标系内绘图网格
          show: false, // 是否显示直角坐标系网格。[ default: false ]
          left: '15%', // grid 组件离容器左侧的距离。
          right: '10%',
          bottom: '28%',
          top: '20%'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#252525', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#252525' // 坐标值得具体的颜色
            }
            // interval: num
          },
          data: xData
        },
        yAxis: {
          type: 'value',
          name: this.$t('mainMorePage.power') + '（Kw）',
          boundaryGap: false,
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              type: 'solid',
              color: '#252525', // 左边线的颜色
              width: '2' // 坐标线的宽度
            }
          },
          axisLabel: {
            textStyle: {
              color: '#252525' // 坐标值得具体的颜色
            }
          }
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 100,
            show: false,
            minValueSpan: 1
          }
        ],
        series: [
          {
            name: this.$t('mainMorePage.power') + '（kw）',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: '#252525'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: 'transparent'
                  },
                  {
                    offset: 1,
                    color: 'transparent'
                  }
                ])
              }
            },
            data: yData
          }
        ]
      })
    },

    // 绘制echarts柱状图
    makePillarEchart (xData, yData) {
      console.log('makePillarEchart')
      this.pillarEchart = echarts.init(document.getElementById('Pillar'))
      this.pillarEchart.setOption({
        color: ['#141414'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top: '20%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: xData,
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            // type : 'category',
            // data : ['10','20','30','40'],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        dataZoom: [{
          type: 'inside'
        }],
        series: [
          {
            name: this.$t('mainMorePage.power') + '（kw）',
            type: 'bar',
            barWidth: '40%',
            data: yData
          }

        ],
        label: {
          normal: {
            show: true,
            position: 'top',
            formatter: '{c}'
          }
        },
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(17, 168,171, 1)'
            }, {
              offset: 1,
              color: 'rgba(17, 168,171, 0.1)'
            }]),
            shadowColor: 'rgba(0, 0, 0, 0.1)',
            shadowBlur: 10
          }
        }
      })
    }
  },

  beforeDestroy () {
    clearTimeout(this.timer)
    this.nav = 0
    this.pillarNav = 0
  }
}
</script>

